@import '../SCSS/custom.css';


.fondo-desplazado-light{
  box-shadow: var(--bs-secondary) 0px 20px;
  background-image: linear-gradient(0deg, var(--bs-secondary) 0%, #5550 95%);
}

.fondo-pagina{
  background-color: var(--bs-light);
  background-image: linear-gradient(0deg, var(--bs-light) 0%, var(--bs-gray) 95%
  );
}



#contenido{
  z-index: 2 !important;
}

.fondo-contenido{
  background-color: var(--bs-secondary);
}

/* scrollbar */
html{
  
  scrollbar-width: auto;
  scrollbar-color: var(--bs-secondary) var(--bs-dark);
}

html::-webkit-scrollbar-thumb{
  background-color: var(--bs-primary);
}
html::-webkit-scrollbar-thumb:hover{
  background-color: var(--bs-primary);
}
html::-webkit-scrollbar-track{
  background-color: var(--bs-primary);
}


/* Define la animación */
@keyframes fadeInUp {
    from {
      opacity: 0;
      transform: translateY(-50px);
    }
    to {
      opacity: 1;
      transform: translateY(0);
    }
  }
  
  /* Aplica la animación al elemento */
.animacion-entrada {
    animation: fadeInUp 0.5s ease-out;
  }
  

/* Estilos para pantallas grandes (md y más grandes) */
@media (min-width: 768px) {
    .col-md-6 {
        text-align: left;  /* Alinea el texto a la izquierda en pantallas grandes */
    }
}

/* Estilos para pantallas más pequeñas (md y menores) */
@media (max-width: 767px) {
    .col-md-6 {
        text-align: center;  /* Alinea el texto al centro en pantallas pequeñas */
    }
}

.rotated{
    transform: rotate(-5deg);
}


.stack-card-primary {
    /*box-shadow: var(--bs-primary) 10px 10px, var(--bs-primary) 10px 10px, var(--bs-primary) 15px 15px, var(--bs-primary) 20px 20px, var(--bs-primary) 25px 25px;*/
    filter: drop-shadow(var(--bs-primary) 10px 10px);
  }

  .stack-card-warning {
    filter: drop-shadow(var(--bs-warning) 10px 10px);
  }
  .stack-card-gray {
